/********************************************** Banner ************************************************/
/*********************************************************************************************************/
.banner {
    width: 100%;
    height: 223px;
    border-radius: 25px;
    position: relative;
    background: #000;

    @media screen and (max-width: 768px) {
        height: 110px;
        border-radius: 10px;
    }

    &_img {
        @extend %img-cover;
        border-radius: 25px;
        opacity: 0.6;

        @media screen and (max-width: 768px) {
            border-radius: 10px;
        }
    }

    &_txt {
        width: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        @media screen and (max-width: 768px) {
            padding-right: 30%;
            padding-left: 16px;
        }
    }
}