/************************ Flex box ************************/
%flex-column {
    display: flex;
    flex-direction: column;
}

%flex-columnCentered {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

%flex-row {
    display: flex;
}

%flex-rowCentered {
    display: flex;
    justify-content: center;
    align-items: center;
}


/************************ Typography ************************/
%font-common {
    font-family: Montserrat;
    line-height: 142.6%;
}

/************************ Images ************************/
%img-cover {
    width: 100%;
    height: 100%;
    object-fit: cover;
}