/********************************************** Ads ************************************************/
/*********************************************************************************************************/
.ads {
    height: 340px;
    border-radius: 10px;
    position: relative;

    @media screen and (max-width: 768px) {
        height: 255px;
        background: none;
    }

    &__img {
        @extend %img-cover;
        border-radius: 10px;
    }

    &__title {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        padding-left: 20px;
        padding-bottom: 20px;
        text-wrap: wrap;
    }
}